import { useEffect, useState, useRef, useCallback } from 'react';
import Icon from "../Icon/Icon"
import Emitter from "../../utils/Emitter";
import './switchNetWorkHint.less'
export default function SwitchNetWorkHint() {
  const [isShow, setIsShow] = useState(false)
  const [name, setName] = useState("")
  const closeBox = () => {
    setIsShow(false);
  }
  const getName = useCallback((data) => {
    setIsShow(true)
    setName(data)
  })
  useEffect(() => {
    Emitter.on("wallet_addEthereumChain_error", getName)
    return () => {
      Emitter.off("wallet_addEthereumChain_error", getName)
    }
  }, [])
  useEffect(()=>{
    let timer;
    if(isShow){
      timer = window.setTimeout(()=>{
        setIsShow(false)
      },8000)
    }
  },[isShow])
  return <>
    {isShow && <div className='network-state'>
      <div className='message-title'>
        <div className=' message-title-text'>
          <Icon token="error" width="20" height="20" />
          Failed to swich networks
        </div>
        <div className='close-icon' onClick={closeBox}>
          <Icon token="close-modal-icon" width='6' height="6" />
        </div>
      </div>
      <div className='trade-info-box'>
        To use Deri Protocol on {name},swich the network in your wallet's settings
      </div>
    </div>}
  </>
}