import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import { Provider } from 'mobx-react';
import reportWebVitals from './reportWebVitals';
import { HashRouter } from 'react-router-dom';
import { DeriEnv } from './lib/web3js/index';
import Wallet from './model/Wallet';
import Trading from './model/Trading';
import PoolManager from './model/PoolManager';
import version from './model/Version';
import intl from './model/Intl';
import loading from './model/Loading';
import type from './model/Type';
import {UseWalletProvider} from 'use-wallet'
import {positions, Provider as AlertProvider } from 'react-alert'
import MessageTemplate from './components/Message/MessageTemplate';
import category from './model/Category';
import { ArbitrumOne, BSC, zkSync } from '@particle-network/common';
import chainlist from './chainlist.json'


const wallet = new Wallet();
const trading = new Trading();
const poolManager = new PoolManager();
if(process.env.NODE_ENV === 'production') {
  DeriEnv.set('prod')
} 
DeriEnv.set('prod')
// DeriEnv.set('dev')
// DeriEnv.set('testnet')
const useWalletOptions = {
  connectors : {
    injected: {
      // chainId: supportChainIds,
      rpc : chainlist
    },
    // fortmatic : {
    //   chainId: 42161,
    //   rpcUrl : 'https://arb-mainnet.g.alchemy.com/v2/tvYr7pUUVOAJEbDGiVMvombLL-5YAhWg',
    //   apiKey: 'pk_live_AF42C183238FA974'
    // },
    walletconnect: {
      // chainId: supportChainIds,
      rpc : chainlist,
    },
    walletlink: {
      chainId: 1,
      url: 'https://mainnet.eth.aragon.network/',
    },
    particle : {
      projectId: process.env.REACT_APP_PROJECT_ID ,
      clientKey: process.env.REACT_APP_CLIENT_KEY ,
      appId: process.env.REACT_APP_APP_ID, 
      chainName: ArbitrumOne.name,
      chainId: ArbitrumOne.id,
      wallet : {
        supportChains : [{
          id : ArbitrumOne.id,
          name : ArbitrumOne.name,
        },{
          id : BSC.id,
          name : BSC.name,
        },{
          id : zkSync.id,
          name : zkSync.name,
        },],
        uiMode : 'dark',
        displayWalletEntry : false,
        // customStyle : {
        //   dark : {
        //     colorOnPrimary : '#203B60',
        //     colorPrimary : '#203B60',
        //   }
        // }
      }
    }
  },
  // autoConnect : true
}

ReactDOM.render(  
  <React.StrictMode>
    <HashRouter>
      <UseWalletProvider {...useWalletOptions}>
        <Provider wallet={wallet} poolManager={poolManager} type={type} category={category} trading={trading} version={version} intl={intl} loading={loading}>
          <AlertProvider position ={positions.TOP_RIGHT}  timeout ={5000}  offset ='30px'  template={MessageTemplate}>
            <App />
          </AlertProvider>
        </Provider>
      </UseWalletProvider>
    </HashRouter>
  </React.StrictMode>
  ,
  document.getElementById('root')
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bi  t.ly/CRA-vitals
reportWebVitals();
