import Dialog from "../Dialog/Dialog";
import Button from "../Button/Button";
import Icon from "../Icon/Icon";
import './accountModal.less'
import { SUPPORTED_WALLETS, NETWORK_MAP } from "../../utils/Constants";
import { useCallback, useState, useEffect } from "react";
import useConfig from '../../hooks/useConfig'
import { useWallet } from "use-wallet";
import { useMemo } from "react";
import { formatAddress } from "../../utils/utils";

export default function AccountModal({ lang, alert, blockExploreUrl, isWrongNetwork, openWalletModal, closeModal }) {
  const [isCopy, setIsCopy] = useState(false)
  const [network, setNetwork] = useState({})
  const { chainId, account, reset, connector, _web3ReactContext } = useWallet()
  const wallet = useWallet()
  const config = useConfig()
  const onCopy = account => {
    if (account) {
      navigator.clipboard.writeText(account);
      alert.success(lang['account-copied'])
      setIsCopy(true)
      setTimeout(() => setIsCopy(false), 5000);
    }
  }

  const curConnector = useMemo(() => {
    const connector = localStorage.getItem("connector")
    if (connector) {
      return JSON.parse(connector)
    }
    return SUPPORTED_WALLETS.injected
  })

  const onDisconnect = useCallback(async () => {
    reset();
    if (connector === 'walletconnect') {
      await _web3ReactContext.connector.close()
    }
    if(window.injected) {
      window.ethereum = window.injected
    }
    openWalletModal();
    closeModal()
  }, [reset])

  const changeWallet = useCallback(async () => {
    onClose();
    reset();
    if(window.injected) {
      window.ethereum = window.injected
    }
    openWalletModal();
  }, [_web3ReactContext, reset])

  const openWebWallet = useCallback(
    () => {
      if (window.particle) {
        window.particle.openWallet()
      }
    },
    [],
  )


  const onClose = () => {
    closeModal();
  }
  useEffect(() => {
    if (wallet.chainId && config[wallet.chainId]) {
      setNetwork(config[wallet.chainId])
    }
  }, [wallet.chainId])
  return (
    <Dialog title={lang['account']} onClose={onClose} className='account-modal'>
      <div className='account-modal-body'>
        <div className='desc'>
          <div>{lang['connected-with']} {SUPPORTED_WALLETS[connector] && SUPPORTED_WALLETS[connector].name}</div>
          {curConnector.connector === 'particleconnect' &&
            <div className="buy-crypto" onClick={openWebWallet}>
              {lang['buy-crypto']}
              <Icon token='particle' height={20} width={20} />
            </div>}
        </div>
        <div className='hash-add'>
          <Icon token={network.code && `${network.icon}`} width='20' />
          <div className="account-add">{formatAddress(account, 10)}</div>
        </div>
        <div className='anchor'>
          {!isWrongNetwork() &&
            <>
              <div>{lang['copy-address']} <Icon token={isCopy ? "copy-s" : "copy"} width='12' onClick={() => onCopy(account)} /></div>
              <div>{`${lang['view-on']} ${network.scanName || network.code}`}<a rel='noreferrer' target='_blank' href={blockExploreUrl}><Icon token='scan' width='12' /> </a></div>
            </>}
        </div>
        <div className='action'>
          <Button label={lang['disconnect']} bgColor='#203B60' outline onClick={onDisconnect} />
          <Button label={lang['change-wallet']} bgColor='#203B60' outline onClick={changeWallet} />
        </div>
      </div>
    </Dialog>
  )
}